import { STramitesService } from './../../services/s-tramites.service';
import { SClientesService } from './../../services/s-clientes.service';
import { Component, OnInit } from "@angular/core";
import Chart from 'chart.js';

@Component({
  selector: "app-dashboard",
  templateUrl: "dashboard.component.html",
  
})
export class DashboardComponent implements OnInit {

  

  public clientes: any;
  public tramites: any;

  p: number = 1;
  p2: number = 1;

  constructor(private sClientes: SClientesService,
              private sTramites: STramitesService) {
    
  }

  ngOnInit() {
    this.getAllClientes();
    this.getAllTramites();
  }

  public getAllClientes(){
    this.clientes = this.sClientes.getAllClientes()
      .subscribe(
        (res:any) => {
          this.clientes = res.clientes;
        },
        erro => {
          console.error(erro);
        }
      );
  }

  public getAllTramites(){
    this.tramites = this.sTramites.getTramites()
        .subscribe(
          (res:any) => {
            this.tramites = res.tramites;
            console.log(this.tramites);
            
          },
          erro => {
            console.error(erro);
          }
        )
  }
  

  
}
