import { Router } from '@angular/router';
import { STramitesService } from './../../services/s-tramites.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lista-tramites',
  templateUrl: './lista-tramites.component.html',
  styles: [
  ]
})
export class ListaTramitesComponent implements OnInit {

  tramites: any;
  constructor(private sTramites: STramitesService,
              private router: Router
              ) {
    this.getTramites();
   }

  ngOnInit(): void {
  }

  getTramites(){
    this.sTramites.getTramites()
      .subscribe(
        (res: any) => {

          this.tramites = res.tramites;
          console.log(this.tramites);
          
        },
        err => console.error(err)
        
      )
  }

  detalles(tramiteID){
    console.log('detalles');
    
    this.router.navigateByUrl(`listaTramites/detalles/${tramiteID}`);
  }
}
