import { STramitesService } from './../../services/s-tramites.service';
import { FormControl, FormBuilder, FormGroup } from '@angular/forms';
import { SClientesService } from './../../services/s-clientes.service';
import { NClienteComponent } from './../n-cliente/n-cliente.component';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-n-tramite',
  templateUrl: './n-tramite.component.html',
  styles: [
  ]
})
export class NTramiteComponent implements OnInit {

   clientes: any;
  selectecCliente: any;
  step = 0;
  formT: FormGroup;
  constructor(private sClientes: SClientesService,
              private fBuilder: FormBuilder,
              private sTramites:STramitesService) {
      this.buildForm();
    
   }

  ngOnInit(): void {
    this.getAllClientes();
  }

  getClienteByID(id: string){
    this.sClientes.getClienteByID(id)
      .subscribe(
        (res: any) => {
          this.step = 1;
          this.selectecCliente = res.cliente;
          
          
          
        },
        err => console.error(err)
        
      )
  }

  private buildForm(){
    this.formT = this.fBuilder.group({
      anio: [''],
      salAnual: [''],
      deduccion: [''],
      rfcPatron: [''],
      tipoPago: [''],
      banco: ['']
    });
  }

  public saveForm(event: Event){
    event.preventDefault();
    let data = this.formT.value;
    data.cliente = this.selectecCliente._id;
    console.log(data);
    this.sTramites.nuevoTramite(data)
      .subscribe(
        (res: any) => {
          console.log(res);
        },
        err => console.log(err)
        
      )

    
  }

  getAllClientes(){
    this.sClientes.getAllClientes()
      .subscribe(
        (res: any) => {
          this.clientes = res.clientes;
          
        },
        err => console.error(err)
        
      )
  }

}
