import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class STramitesService {
  
  private base_url = 'https://genefapi.herokuapp.com/api/';

  constructor(private http: HttpClient) { }

  public nuevoTramite(data){
    return this.http.post(`${this.base_url}tramites`,data);
  }
  public getTramites(){
    return this.http.get(`${this.base_url}tramites`);
  }

  public getTramite(tramiteID){
    return this.http.get(`${this.base_url}tramites/${tramiteID}`);
  }
}
