import { SMunicipiosService } from './../../services/s-municipios.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-n-cliente',
  templateUrl: './n-cliente.component.html',
  styles: [
  ]
})
export class NClienteComponent implements OnInit {

  public estados:any;
  constructor(private sMunicipios: SMunicipiosService) {
    this.getEstados();
   }

  ngOnInit() {}


  public getEstados(){
    this.sMunicipios.getEstados()
      .subscribe(
        (res: any) => {
          this.estados = res.response.estado;
          
        },
        err => {
          console.error(err);
          
        }
      )
  }

  public getMunicipios(estado: string){
    this.sMunicipios.getMunicipios(estado)
      .subscribe(
        (res: any) => {
          console.log(res);
        },
        err => {
          console.error(err);
          
        }
      )
  }

}
