import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SFacturasService {
  private base_url = 'https://genefapi.herokuapp.com/api/';
  constructor(private http: HttpClient) { }

  getFacturasByTramite(tramiteID){
    return this.http.get(`${this.base_url}facturas/tramite/${tramiteID}`);
  }

  getFacturaByID(factura){
    return this.http.get(`${this.base_url}facturas/${factura}`);
  }
  
  getMovimientos(factura){
    return this.http.get(`${this.base_url}facturas/movimientos/${factura}`);
  }
}
